import PropTypes from "prop-types";
import React, { memo, useState } from "react";
import { PaginationSectionWrapper } from "./style";
import Pagination from "@mui/material/Pagination";
import { useDispatch } from "react-redux";
import { fetchEntireData } from "@/store/modules/entire/actionCreater";

const PaginationSection = memo((props) => {
  const [page, setPage] = useState(1);
  const { currentPage = 1, totalCount } = props.paginationInfo;
  const totalPage = Math.ceil(totalCount / 20);
  const dispatch = useDispatch();
  function changePageHandle(event, value) {
    dispatch(fetchEntireData(value));
    setPage(value);
    window.scroll(0, 0);
  }
  return (
    <PaginationSectionWrapper>
      <div className="pageNav">
        <Pagination count={totalPage} page={page} onChange={changePageHandle} />
      </div>
      <div className="navDesc">
        第{(currentPage - 1) * 20 + 1}-{currentPage * 20}个房源,共超过
        {totalCount}个
      </div>
    </PaginationSectionWrapper>
  );
});

PaginationSection.propTypes = {
  paginationInfo: PropTypes.object,
};

export default PaginationSection;
