import RoomItem from "@/components/room-item";
import PropTypes from "prop-types";
import React, { memo } from "react";
import { AllRoomsListWrapper } from "./style";

const AllRoomsList = memo((props) => {
  const { roomsList, totalCount, isLoading } = props.roomsInfo;
  return (
    <AllRoomsListWrapper>
      <h2 className="listTitle">{totalCount}多处住处</h2>
      <div className="allRoomsList">
        {roomsList?.map((item, index) => (
          <RoomItem key={item._id} roomData={item} itemWidth={"20%"} />
        ))}
      </div>
      {isLoading && <div className="coverBackgroud"></div>}
    </AllRoomsListWrapper>
  );
});

AllRoomsList.propTypes = {
  roomsInfo: PropTypes.object,
};

export default AllRoomsList;
