import PropTypes from "prop-types";
import React, { memo, useState } from "react";

import filterData from "@/assets/data/filter_data.json";
import { FilterSectionWrapper } from "./style";
import classNames from "classnames";

const FilterSection = memo((props) => {
  const [buttonActive, setButtonActive] = useState([]);
  function filterClickHander(item, index) {
    let newItem = [...buttonActive];
    if (newItem.includes(item)) {
      newItem = newItem.filter((i) => i !== item);
    } else {
      newItem.push(item);
    }
    setButtonActive([...newItem]);
  }
  return (
    <FilterSectionWrapper>
      <div className="buttonList">
        {filterData.map((item, index) => (
          <div
            key={index}
            onClick={(e) => {
              filterClickHander(item, index);
            }}
            className={classNames("filter_item", {
              active: buttonActive.includes(item),
            })}
          >
            {item}
          </div>
        ))}
      </div>
      <button>显示地图</button>
    </FilterSectionWrapper>
  );
});

FilterSection.propTypes = {};

export default FilterSection;
