import { fetchEntireData } from "@/store/modules/entire/actionCreater";
import { changeHeaderConfig } from "@/store/modules/main";
import { isEmptyObeject } from "@/utils";
import React, { memo, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import AllRoomsList from "./childrens/all-rooms-list";
import FilterSection from "./childrens/filter-section";
import PaginationSection from "./childrens/pagination-section";
import { EntireWrapper } from "./style";

const Entire = memo(() => {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(fetchEntireData());
    dispatch(changeHeaderConfig({ isFixed: true }));
  }, [dispatch]);
  const entireInfo = useSelector((state) => state.entire);
  return (
    <EntireWrapper>
      <FilterSection />
      {isEmptyObeject(entireInfo) && <AllRoomsList roomsInfo={entireInfo} />}
      {isEmptyObeject(entireInfo) && (
        <PaginationSection paginationInfo={entireInfo} />
      )}
    </EntireWrapper>
  );
});

export default Entire;
