import styled from "styled-components";
export const RoomItemWrapper = styled.div`
  width: ${(props) => props.itemWidth};
  flex-shrink: 0;
  padding: 0 8px;
  box-sizing: border-box;
  margin-bottom: 15px;
  .inner {
    width: 100%;
  }
  .slider {
    position: relative;
  }
  .control {
    position: absolute;
    z-index: 9;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    .toRight,
    .toLeft {
      cursor: pointer;
      height: 100%;
      color: white;
      display: flex;
      align-items: center;
      width: 50px;
    }
    .toLeft {
      background-image: linear-gradient(
        -90deg,
        rgba(0, 0, 0, 0) 1%,
        rgb(0, 0, 0, 0.8) 100%
      );
    }
    .toRight {
      background-image: linear-gradient(
        90deg,
        rgba(0, 0, 0, 0) 1%,
        rgb(0, 0, 0, 0.8) 100%
      );
      flex-direction: row-reverse;
    }
  }
  .indicator {
    position: absolute;
    z-index: 99;
    bottom: 5%;
    width: 30%;
    margin: 0 auto;
    right: 0;
    left: 0;
  }
  .dotWrapper {
    width: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    .dotSelect {
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background-color: rgba(255, 255, 255, 0.8);
    }
    .dot-active {
      width: 8px;
      height: 8px;
    }
  }
  img {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
  }
  .cover {
    border-radius: 3px;
    box-sizing: border-box;
    padding: 66.6% 0 0;
    overflow: hidden;
    position: relative;
  }
  .desc {
    margin-top: 8px;
    color: ${(props) => props.descColor};
    font-weight: 600;
    font-size: 8px;
  }
  .title {
    height: 35px;
  }
  .roomName {
    font-size: 16px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    cursor: pointer;
    :hover {
      text-decoration: underline;
    }
  }
  .price {
  }
  .price::after {
    content: "/晚";
    font-size: 12px;
  }
  .title,
  .price,
  .desc {
    padding: 4px 0;
  }
`;
