import * as actionTypes from "./constants";
import { getEntireRoomsList } from "@/services/modules/entire";
export const changePageAction = (page) => ({
  type: actionTypes.CHANGE_ENTIRE_PAGE,
  page,
});
export const changeTotalAmountAction = (totalAmount) => ({
  type: actionTypes.CHANGE_ENTIRE_TOTAL_AMOUNT,
  totalAmount,
});
export const changeRoomsList = (roomList) => ({
  type: actionTypes.CHANGE_ENTIRE_ROOMSLIST,
  roomList,
});
export const changeIsLoading = (isLoading) => ({
  type: actionTypes.CHANGE_ENTIRE_ISLOADING,
  isLoading,
});
export const fetchEntireData = (page = 1) => {
  return async (dispatch, getstate) => {
    dispatch(changePageAction(page));
    dispatch(changeIsLoading(true));
    const res = await getEntireRoomsList((page - 1) * 20);
    dispatch(changeIsLoading(false));
    const roomList = res.list;
    const totalCount = res.totalCount;
    dispatch(changeRoomsList(roomList));
    dispatch(changeTotalAmountAction(totalCount));
  };
};
