import IconArrowLeft from "@/assets/svg/icon-arrow-left";
import IconArrowRight from "@/assets/svg/icon-arrow-right";
import Indicator from "@/base-ui/indicator";
import { changeRoomDetailInfo } from "@/store/modules/details";
import Rating from "@mui/material/Rating";
import { Carousel } from "antd";
import classNames from "classnames";

import PropTypes from "prop-types";
import React, { memo, useState, useRef } from "react";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import { RoomItemWrapper } from "./style";

const RoomItem = memo((props) => {
  const { roomData = [], itemWidth = "25%" } = props;
  const [isControlShow, setIsControlShow] = useState(false);
  const [selectIndex, setSelectIndex] = useState(0);
  const carouselRef = useRef();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  function mouseMoveHandle(isOver) {
    isOver ? setIsControlShow(true) : setIsControlShow(false);
  }
  function switchPictureHandle(isRight, event) {
    let newIndex = selectIndex;
    if (isRight) {
      carouselRef.current.next();
      newIndex++;
      if (newIndex > roomData.picture_urls.length - 1) newIndex = 0;
      setSelectIndex();
    } else {
      carouselRef.current.prev();
      newIndex--;
      if (newIndex < 0) newIndex = roomData.picture_urls.length - 1;
    }
    setSelectIndex(newIndex);
    event.stopPropagation();
  }
  function indicatorClickHandle(index) {
    setSelectIndex(index);
    carouselRef.current.goTo(index);
  }
  function goToDetailsHandle(e) {
    navigate("/detail");
    dispatch(changeRoomDetailInfo(roomData));
  }
  const shufflingFigure = (
    <div
      className="slider"
      onMouseMove={(e) => mouseMoveHandle(true)}
      onMouseOut={(e) => mouseMoveHandle(false)}
    >
      <Carousel dots={false} ref={carouselRef}>
        {roomData.picture_urls?.map((item) => (
          <div className="cover" key={item}>
            <img src={item} alt="" />
          </div>
        ))}
      </Carousel>
      {isControlShow && (
        <div className="control" onClick={goToDetailsHandle}>
          <div
            className="toLeft"
            onClick={(e) => switchPictureHandle(false, e)}
          >
            <IconArrowLeft size={{ height: 40, width: 40 }} />
          </div>
          <div
            className="toRight"
            onClick={(e) => switchPictureHandle(true, e)}
          >
            <IconArrowRight size={{ height: 40, width: 40 }} />
          </div>
        </div>
      )}
      <Indicator index={selectIndex}>
        {roomData.picture_urls?.map((item, index) => (
          <div
            className="dotWrapper"
            key={item}
            onClick={(e) => {
              indicatorClickHandle(index);
            }}
          >
            <div
              onClick={(e) => {
                indicatorClickHandle(index);
              }}
              className={classNames("dotSelect", {
                "dot-active": selectIndex === index,
              })}
            ></div>
          </div>
        ))}
      </Indicator>
    </div>
  );
  const singleFighue = (
    <div className="cover">
      <img src={roomData.picture_url} alt="" />
    </div>
  );
  return (
    <RoomItemWrapper
      descColor={roomData?.verify_info?.text_color || "#39576a"}
      itemWidth={itemWidth}
    >
      <div className="inner">
        {roomData.picture_urls ? shufflingFigure : singleFighue}
        <div className="desc">{roomData.verify_info.messages.join("")}</div>
        <div className="title">
          <h2 className="roomName">{roomData.name}</h2>
        </div>
        <div className="price">{`$${roomData.price}`}</div>
        <div>
          <Rating
            name="read-only"
            sx={{ fontSize: "12px", color: "#00848A" }}
            value={roomData.star_rating ?? 5}
            precision={0.1}
            readOnly
          />
          <span className="reviewCount">{roomData.review_count}</span>
          {roomData.bottom_info && <span>{roomData.bottom_info.content}</span>}
        </div>
      </div>
    </RoomItemWrapper>
  );
});

RoomItem.propTypes = {
  roomData: PropTypes.object,
};

export default RoomItem;
